.carousel-container {

    height: 90%;
    position: relative;
    overflow: hidden;

}

.carousel-slides {

    height: 100%;
    position: relative;

}

.carousel-slide {

    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    bottom: 0;

}

.carousel-content-container {

    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    opacity: 0;

    transition: opacity 1s ease-in-out;

}

